import React, { Component } from 'react';
import {connect} from "react-redux";
import PropTypes from 'prop-types';
import { FaFacebookF, FaInstagram, FaViber, FaRegEnvelope } from "react-icons/fa";

import MainMenu from "../MainMenu";
import SideMainMenu from "../SideMainMenu";
import Footer from "../Footer";

import "./ContactsPage.css";

function mapStateToProps(state) {
	return {
		ui: state.main.ui,
		height: state.main.height
	}
}

// Component representing Contacts page of the site
class ContactsPage extends Component {
	render() {
		// Set min-height style to move the footer to the end of the page
		const blockHeight = this.props.height - 160;
		const contactsPageStyle = {minHeight: blockHeight + "px"};

		const pageHeader = this.props.ui.contactsPage.header;

		return (
			<div className="contacts_page">
				<MainMenu active="contacts" />
				<SideMainMenu />
				<div style={contactsPageStyle}>
					<div className="main_column">
						<h2>{pageHeader}</h2>
						<div className="links">
							<a href="https://www.facebook.com/karmanniydroog/">
								<div className="link_item">
									<div className="icon_wrap">
										<FaFacebookF />
									</div>
									<div className="link_text">https://www.facebook.com/karmanniydroog/</div>
								</div>
							</a>
							<a href="https://www.instagram.com/karmanniy_droog/">
								<div className="link_item">
									<div className="icon_wrap">
										<FaInstagram />
									</div>
									<div className="link_text">https://www.instagram.com/karmanniy_droog/</div>
								</div>
							</a>
							<a href="tel:+380676995858">
								<div className="link_item">
									<div className="icon_wrap">
										<FaViber />
									</div>
									<div className="link_text">+380676995858</div>
								</div>
							</a>
							<a href="mailto: pocket.friend.ua@gmail.com">
								<div className="link_item">
									<div className="icon_wrap">
										<FaRegEnvelope />
									</div>
									<div className="link_text">pocket.friend.ua@gmail.com</div>
								</div>
							</a>
						</div>
					</div>
				</div>
				<Footer />
			</div>
		)
	}
}

ContactsPage.propTypes = {
	height: PropTypes.number.isRequired,
	ui: PropTypes.shape({
		contactsPage: PropTypes.shape({
			header: PropTypes.string.isRequired
		})
	})
}

export default connect(mapStateToProps)(ContactsPage);