import React, { Component } from 'react';
import {connect} from "react-redux";
import PropTypes from "prop-types";
import {FaAngleDoubleLeft, FaAngleDoubleRight} from "react-icons/fa";

import actions from "../../actions";

import "./FilterMenu.css";

function mapStateToProps(state) {
	return {
		ui: state.main.ui,
		groups: state.main.groups,
		allSelectFilter: state.main.allSelectFilter,
		language: state.main.language,
		search: state.main.search,
		showFilterMenu: state.main.showFilterMenu,
		filterMethod: state.main.filterMethod
	}
}

//Component representing sliding sidebar filter menu
class FilterMenu extends Component {
	
	// change current checkbox
	changeCheckbox = (event) => {
		this.props.dispatch(actions.filterChange(event.target.name, event.target.checked));
	}

	// change all checkboxes
	changeAllCheckbox = (event) => {
		this.props.dispatch(actions.filterChange(event.target.name, event.target.checked));
	}

	// change search string value
	changeSearch = (event) => {
		this.props.dispatch(actions.searchChange(event.target.value));
	}

	// show/hide filter menu
	toggleFilterMenu = (event) => {
		this.props.dispatch(actions.toggleFilterMenu());
	}

	// change filtering method (search string / group filter)
	radioChange = (value) => {
		if (this.props.filterMethod !== value) {
			this.props.dispatch(actions.changeFilterMethod(value));
		}
	}

	render() {
		const filterUi = this.props.ui.galleryPage.filterMenu;
		const groups = this.props.groups;
		const groupNames = Object.keys(groups);
		const language = this.props.language;
		const filterMenuClass = "filter_menu " + this.props.showFilterMenu;
		let searchFilterClass = "search_filter";
		let groupFilterClass = "group_filter";
		if (this.props.filterMethod === "search") {
			searchFilterClass += " active";
		}
		else {
			groupFilterClass += " active";
		}
		const showhideTitle = this.props.showFilterMenu === "show" ?
								filterUi.closeButtonTitle :
								filterUi.openButtonTitle;

		return (
			<div className={filterMenuClass}>
				<div className="wrapper">
					<div className="side_slider" style={{height: window.innerHeight}}>
						<div className="body">
							<div className={searchFilterClass} onClick={() => {this.radioChange("search")}}>
								<p>{filterUi.choseFilterMethod}</p>
								<div>
									<input 
										className="search_radio"
										type="radio" 
										value="search"
										onChange={() => {this.radioChange("search")}}
										checked={this.props.filterMethod === "search"}
									/>
									<span>{filterUi.searchByString}</span>
									<br/>
									<input 
										className="search_string"
										type="text"
										placeholder={filterUi.searchPlaceholder} 
										value={this.props.search} 
										onChange={this.changeSearch}
									/>
								</div>
							</div>
							<div className={groupFilterClass} onClick={() => {this.radioChange("group")}}>
								<input 
									className="filter_radio"
									type="radio" 
									value="group"
									onChange={() => {this.radioChange("group")}}
									checked={this.props.filterMethod === "group"}
								/>
								<span>{filterUi.filterByGroups}</span>
								<div className="all_checkbox">
									<input
										type="checkbox" 
										name="all" 
										checked={this.props.allSelectFilter} 
										onChange={this.changeAllCheckbox}
									/>
									<span>All</span>
								</div>
								{
									groupNames.map((key, index) => {
										return (
											<div key={index} className="checkbox">
												<input 
													type="checkbox" 
													name={key} 
													checked={groups[key].value} 
													onChange={this.changeCheckbox}
												/>
												<span>{groups[key].lang[language].name}</span>
											</div>
										)
									})
								}
							</div>
						</div>
						<div className="showhide" onClick={this.toggleFilterMenu} title={showhideTitle}>
							{
								this.props.showFilterMenu === "show" ?
								<FaAngleDoubleLeft /> :
								<FaAngleDoubleRight />
							}
						</div>
					</div>
				</div>
			</div>
		)
	}
}

FilterMenu.propTypes = {
	ui: PropTypes.shape({
		galleryPage: PropTypes.shape({
			filterMenu: PropTypes.shape({
				closeButtonTitle: PropTypes.string.isRequired,
				openButtonTitle: PropTypes.string.isRequired,
				searchByString: PropTypes.string.isRequired,
				searchPlaceholder: PropTypes.string.isRequired,
				filterByGroups: PropTypes.string.isRequired
			})
		})
	}),
	groups: PropTypes.object.isRequired,
	allSelectFilter: PropTypes.bool.isRequired,
	language: PropTypes.string.isRequired,
	search: PropTypes.string.isRequired,
	showFilterMenu: PropTypes.string.isRequired,
	filterMethod: PropTypes.string.isRequired,
}

export default connect(mapStateToProps)(FilterMenu);