import React, { Component } from 'react';
import {connect} from "react-redux";
import {FaArrowLeft, FaArrowRight} from "react-icons/fa";
import PropTypes from "prop-types";

import actions from "../../actions";

import "./PagingSection.css";

function mapStateToProps(state) {
	return {
		page: state.main.page,
		perPage: state.main.perPage
	}
}

// block containing page number elements
class PagingSection extends Component {

	onPageClick = (targetPage) => {
		const page = parseInt(this.props.page);
		if (page !== targetPage) {
			window.scroll(0, 0);
			this.props.dispatch(actions.setGalleryPage(targetPage));
		}
	}

	setPageList = () => {
		const pagesRange = 2;
		const currPage = this.props.page;
		const lastPage = this.props.lastPage;
		const pageStart = currPage - pagesRange >= 0 ? currPage - pagesRange : 0;
		const pageEnd = currPage + pagesRange <= lastPage ? currPage + pagesRange : lastPage;
		let tempPage = pageStart;
		let result = [];
		do {
			result.push(tempPage);
			tempPage += 1;
		} while (tempPage <= pageEnd);
		return result;
	}

	render() {
		const pages = this.setPageList();
		const currentPage = parseInt(this.props.page);
		const lastPage = this.props.lastPage;

		return (
			<div className="paging_section">
				<div className="pages">
					{
						(currentPage > 0) &&
						<div className="page" onClick={() => {this.onPageClick(currentPage - 1)}}>
							<FaArrowLeft />
						</div>
					}
					{
						pages.map((element, index) => {
							let pageClass="page";
							if (element === currentPage) {
								pageClass += " current";
							}
							return (
								<div key={index} 
									className={pageClass} 
									onClick={() => {this.onPageClick(element)}}
								>
									{element + 1}
								</div>
							)
						})
					}
					{
						(currentPage < lastPage) &&
						<div className="page" onClick={() => {this.onPageClick(currentPage + 1)}}>
							<FaArrowRight />
						</div>
					}					
				</div>
			</div>
		)
	}
}

PagingSection.propTypes = {
	page: PropTypes.number.isRequired,
	perPage: PropTypes.number.isRequired,
	lastPage: PropTypes.number.isRequired
}

export default connect(mapStateToProps)(PagingSection);