import React, { Component } from 'react';
import {connect} from "react-redux";
import PropTypes from "prop-types";

import GalleryItem from "../GalleryItem";
import PagingSection from "../PagingSection";

import "./GallerySection.css";

function mapStateToProps(state) {
	return {
		products: state.main.products,
		groups: state.main.groups,
		allSelectFilter: state.main.allSelectFilter,
		search: state.main.search,
		filterMethod: state.main.filterMethod,
		page: state.main.page,
		perPage: state.main.perPage,
		height: state.main.height
	}
}

// Main section of gallery page 
class GallerySection extends Component {

	// filter products by one of filtering methods
	filterProducts() {
		let result = this.props.products
			.slice()
			.filter((element) => {
				if (this.props.filterMethod === "search") {
					return this.searchCheck(element);
				}
				else {
					return this.groupCheck(element);
				}
			});

		return result;
	}

	// filter by search string
	searchCheck(element) {
		let result = false;
		let search = this.props.search;

		if (search !== "") {
			//search by id
			if ("" + element.id === search) {
				result = true;
			}

			//search by tag
			element.tags.forEach((tag) => {
				if (tag === search) {
					result = true;
				}
			});

			// search by name/title
			Object.keys(element.lang).forEach((language) => {
				const re = RegExp(search, "i");
				if (element.lang[language].title.search(re) !== -1) {
					result = true;
				}
			});
		}
		else {
			result = true;
		}

		return result;
	}

	// filter by groups
	groupCheck(element) {
		let result = false;

		if(this.props.allSelectFilter) {
			result = true;
		}
		else {
			if (this.props.groups[element.group] && this.props.groups[element.group].value) {
				result = true;
			}
		}

		return result;
	}

	// set an array of page numbers range relative to the current page
	setPageList(list, page, perPage) {
		let startIndex = page * perPage;
		let endIndex = startIndex + perPage;
		if (endIndex > list.length) {
			endIndex = list.length;
		}
		return list.slice(startIndex, endIndex);
	}

	// set count of pages
	pagesCount(list, perPage) {
		let result = 0;
		let count = list.length;

		while (count > 0) {
			result += 1;
			count -= perPage;
		}

		return result;
	}

	render() {
		let list = this.filterProducts();
		const showPages = list.length > 12 ? true : false;
		let pageList = this.setPageList(list, this.props.page, this.props.perPage);
		let lastPage = this.pagesCount(list, this.props.perPage) - 1;
		const blockHeight = this.props.height - 160;
		const gallSectStyle = {minHeight: blockHeight + "px"};

		return (
			<div className="gallery_section" style={gallSectStyle}>
				{
					showPages && <PagingSection lastPage={lastPage} />
				}
				<div className="items">
				{
					pageList.map((element, index) => {
						return <GalleryItem key={index} prodId={element.id}/>
					})					
				}
				</div>
				{
					showPages && <PagingSection lastPage={lastPage} />
				}
			</div>
		)
	}
}

GallerySection.propTypes = {
	products: PropTypes.array.isRequired,
	filterMethod: PropTypes.string.isRequired,
	search: PropTypes.string.isRequired,
	allSelectFilter: PropTypes.bool.isRequired,
	groups: PropTypes.object.isRequired,
	height: PropTypes.number.isRequired
}

export default connect(mapStateToProps)(GallerySection);