import React, { Component } from 'react';
import {connect} from "react-redux";
import PropTypes from 'prop-types';

import GalleryItem from "../GalleryItem";

import "./FavoritsSection.css";

function mapStateToProps(state) {
	return {
		products: state.main.products,
		height: state.main.height
	}
}

// Component representing main section of Favorits page, which contains all items (GalleryItem)
// wich have parameter favorits=true
class FavoritsSection extends Component {

	// set array of favorit products 
	setFavoritList() {
		let result = this.props.products.filter((element) => {
			return element.favorits === true;
		});
		return result;
	}

	render() {
		let favoritList = this.setFavoritList();

		// Set min-height style to move the footer to the end of the page
		const blockHeight = this.props.height - 160;
		const faivSectStyle = {minHeight: blockHeight + "px"};

		return (
			<div className="favorits_section" style={faivSectStyle}>
				<div className="items">
				{
					favoritList.map((element, index) => {
						return <GalleryItem key={index} prodId={element.id}/>
					})					
				}
				</div>
			</div>
		)
	}
}

FavoritsSection.propTypes = {
	products: PropTypes.array.isRequired,
	height: PropTypes.number.isRequired
}

export default connect(mapStateToProps)(FavoritsSection);